'use strict';

var Reflux = require('reflux');
var FilteredProjectsStore = require('./filteredProjectsStore');


function _extractPointFeatures(feature) {
  return feature.geometry.coordinates.map(function(coords) {
    return {
      type: 'Feature',
      id: feature.id,
      geometry: {
        type: 'Point',
        coordinates: coords,
      },
      properties: feature.properties,
    };
  });
}

function _arrayJoin(a, b) {
  return a.concat(b);
}

function multiPointsToFeatureCollection(arr) {
  var pointFeatures = arr
    .map(_extractPointFeatures)
    .reduce(_arrayJoin, []);

  return {
    type: 'FeatureCollection',
    features: pointFeatures,
  };
}


var LocationsStore = Reflux.createStore({

  init: function() {
    this.data = this.getInitialState();
    this.listenTo(FilteredProjectsStore, this.updateProjects);
  },

  updateProjects: function(projects) {
    this.data = multiPointsToFeatureCollection(projects);
    this.emit();
  },

  emit: function() {
    this.trigger(this.data);
  },

  getInitialState: function() {
    return [];
  },

});


module.exports = LocationsStore;
